import { SSMClient, GetParameterCommand } from '@aws-sdk/client-ssm';
import { NextRequest } from 'next/server';

const ssmClient = new SSMClient({ region: 'eu-west-1' });
const tenantCache = new Map<string, { tenant: string; timestamp: number }>();
const CACHE_TTL = 5 * 60 * 1000; // 5 minutes

const DEFAULT_TENANT = 'carousel-labs';

/**
 * Extract tenant from request using hierarchy:
 * 1. X-Tenant-Id header (explicit tenant override)
 * 2. Host domain lookup (e.g., hringekjan.prod.carousellabs.co -> hringekjan)
 * 3. Default to carousel-labs
 *
 * Follows backend-kit tenant extraction patterns for consistency
 */
export async function resolveTenantFromRequest(request: NextRequest, stage: string): Promise<string> {
  // Priority 1: X-Tenant-Id header (matches backend-kit extractTenantFromHeader)
  const headerTenant = request.headers.get('x-tenant-id')?.toLowerCase().trim();
  if (headerTenant) {
    return headerTenant;
  }

  // Priority 2: Host domain lookup
  const host = request.headers.get('host') || '';
  const domainTenant = extractTenantFromDomain(host);
  if (domainTenant) {
    return domainTenant;
  }

  // Default fallback
  return DEFAULT_TENANT;
}

/**
 * Extract tenant from domain patterns:
 * Tenant-specific TLDs:
 * - carousel.{stage}.carousellabs.co -> carousel-labs
 * - api.{stage}.carousellabs.co -> carousel-labs (shared)
 * - carousel.hringekjan.is -> hringekjan
 * - api.hringekjan.is -> hringekjan (shared)
 *
 * localhost/127.0.0.1 -> null (uses default)
 */
function extractTenantFromDomain(host: string): string | null {
  if (!host || host.includes('localhost') || host.includes('127.0.0.1')) {
    return null; // Use default for local development
  }

  // Pattern 1: carousel-labs tenant using carousellabs.co
  // Match: carousel.{stage}.carousellabs.co OR api.{stage}.carousellabs.co
  const carouselLabsMatch = host.match(/^(carousel|api)\.(dev|prod|staging)\.carousellabs\.co$/i);
  if (carouselLabsMatch) {
    return 'carousel-labs';
  }

  // Pattern 2: hringekjan tenant using hringekjan.is
  // Match: carousel.hringekjan.is OR api.hringekjan.is
  const hringekjanMatch = host.match(/^(carousel|api)\.hringekjan\.is$/i);
  if (hringekjanMatch) {
    return 'hringekjan';
  }

  // Pattern 3: S3 bucket pattern: {tenant}.s3.{region}.amazonaws.com
  const s3Match = host.match(/^([a-z0-9-]+)\.s3/i);
  if (s3Match) {
    return s3Match[1].toLowerCase();
  }

  return null;
}

/**
 * Load tenant configuration from SSM with caching
 * Tries tenant-specific config first, then falls back to platform config
 */
export async function loadTenantConfig(tenant: string, stage: string) {
  const cacheKey = `${tenant}:${stage}`;

  // Check cache first
  const cached = tenantCache.get(cacheKey);
  if (cached && Date.now() - cached.timestamp < CACHE_TTL) {
    return cached;
  }

  try {
    // Try tenant-specific config first
    const param = await ssmClient.send(
      new GetParameterCommand({
        Name: `/tf/${stage}/${tenant}/services/bg-remover/config`,
        WithDecryption: true,
      })
    );

    const config = JSON.parse(param.Parameter?.Value || '{}');
    const result = { tenant, ...config, timestamp: Date.now() };
    tenantCache.set(cacheKey, result);
    return result;
  } catch (error) {
    // Fallback to platform config if tenant config doesn't exist
    try {
      const param = await ssmClient.send(
        new GetParameterCommand({
          Name: `/tf/${stage}/platform/bg-remover/config`,
          WithDecryption: true,
        })
      );

      const config = JSON.parse(param.Parameter?.Value || '{}');
      const result = { tenant, ...config, timestamp: Date.now() };
      tenantCache.set(cacheKey, result);
      return result;
    } catch (fallbackError) {
      console.error(`Failed to load config for tenant ${tenant}`, { error, fallbackError });
      // Return minimal config - service should still work with defaults
      return { tenant, timestamp: Date.now() };
    }
  }
}
